<template>
  <div id="colleagues-add-dialog-component">
    <el-dialog title="Add Colleague" v-model="visible" width="40%" top="45px">
      <div class="inputs-container">
        <el-row :gutter="30">
          <el-col :span="24">
            <p class="label">Username</p>
            <el-input v-model="form.username"> </el-input>
          </el-col>
          <el-col :span="24">
            <p class="label">Team</p>
            <el-select v-model="form.teams" multiple>
              <el-option
                v-for="item in teamOptions"
                :key="item.id"
                :label="item.name"
                :value="item.id"
                :loading="!isLoaded"
              ></el-option>
            </el-select>
          </el-col>
          <el-col :span="24">
            <p class="label">Email</p>
            <el-input v-model="form.email" type="email"></el-input>
          </el-col>
          <el-col :span="24">
            <p class="label">Password</p>
            <el-input v-model="form.password" type="password"> </el-input>
          </el-col>
          <el-col :span="24">
            <p class="label">Confirm Password</p>
            <el-input v-model="confirmPassword" type="password"> </el-input>
          </el-col>
        </el-row>
      </div>
      <template #footer>
        <span class="dialog-footer">
          <el-button type="info" @click="visible = false">Cancel</el-button>
          <el-button type="success" @click="onConfirm">
            Done
          </el-button>
        </span>
      </template>
    </el-dialog>
  </div>
</template>

<script lang="ts">
import { defineComponent, ref, reactive } from "vue";

import { CreateMembersParams } from "@/typings/auth";
import { postCreateMembers } from "@/apis/auth";
import { ElNotification } from "element-plus";
import { getMemberTeams } from "@/apis/member";
import { MemberTeam } from "@/typings/member";

const Component = defineComponent({
  name: "ColleaguesAddDialog",
  setup(_, { emit }) {
    const visible = ref(false);

    const teamOptions = ref<MemberTeam[]>([]);
    const isLoaded = ref(false);
    const confirmPassword = ref("");
    const form = reactive<CreateMembersParams>({
      username: "",
      password: "",
      email: "",
      teams: []
    });

    const init = async () => {
      const { data } = await getMemberTeams();
      teamOptions.value = data;
      isLoaded.value = true;
    };

    const open = async () => {
      visible.value = true;
      await init();
    };

    const onConfirm = async () => {
      if (/[^\w@+-.]/g.test(form.username)) {
        ElNotification({
          type: "error",
          title: "Error",
          message: `Username should follow rules: 150 characters or fewer. Letters, digits and @/./+/-/_ only`
        });
        return;
      }
      if (form.password !== confirmPassword.value) {
        ElNotification({
          type: "error",
          title: "Error",
          message: `Confirm password is not the same as password.`
        });
        return;
      }
      const params: CreateMembersParams = {
        username: form.username,
        password: form.password,
        email: form.email,
        teams: form.teams
      };
      await postCreateMembers(params);
      ElNotification({
        type: "success",
        title: "Success",
        message: `Add colleague ${form.username} successful.`
      });
      emit("load");

      visible.value = false;
    };

    return {
      form,
      confirmPassword,
      teamOptions,
      visible,

      open,
      onConfirm
    };
  }
});

export default Component;
</script>

<style scoped lang="stylus">
#colleagues-add-dialog-component
  .inputs-container
    padding 0 10%
    .label
      text-align start
      font-size $font-size-large
      font-weight 500
      padding 0 8px
      margin-bottom 15px

    :deep() .el-col
      margin-bottom 30px

    :deep() .el-input
      width 100%

    :deep() .el-select
      width 100%
</style>
