import request from "@/apis/http";
import { AxiosPromise } from "axios";
import { MemberTeam } from "@/typings/member";

// Haven't id means get self teams data by token.
export function getMemberTeams(id?: number): AxiosPromise<MemberTeam[]> {
  if (id) {
    return request({
      url: `/member/${id}/teams`,
      method: "GET"
    });
  }
  return request({
    url: "/member/teams",
    method: "GET"
  });
}
