const getStatusButtonStyles = (text: string) => {
  if (text === "activated") return "activated";
  if (text === "expired") return "expired";
  return "";
};

const getStatusText = (text: string) => {
  if (text === "activated") return "Activated";
  if (text === "expired") return "Expired";
  return "";
};

const Component = defineComponent({
  name: "BrandTable",
  props: {
    maxHeight: {
      type: String,
      default: "auto"
    }
  },
  setup() {
    const router = useRouter();

    const onUsernameClick = async (id: number) => {
      await router.push({ name: "UserProfile", params: { id } });
    };

    return {
      tableData,

      onUsernameClick,

      getStatusText,
      getStatusButtonStyles
    };
  }
});

export default Component;
