
import { defineComponent, ref, reactive } from "vue";

import { CreateMembersParams } from "@/typings/auth";
import { postCreateMembers } from "@/apis/auth";
import { ElNotification } from "element-plus";
import { getMemberTeams } from "@/apis/member";
import { MemberTeam } from "@/typings/member";

const Component = defineComponent({
  name: "ColleaguesAddDialog",
  setup(_, { emit }) {
    const visible = ref(false);

    const teamOptions = ref<MemberTeam[]>([]);
    const isLoaded = ref(false);
    const confirmPassword = ref("");
    const form = reactive<CreateMembersParams>({
      username: "",
      password: "",
      email: "",
      teams: []
    });

    const init = async () => {
      const { data } = await getMemberTeams();
      teamOptions.value = data;
      isLoaded.value = true;
    };

    const open = async () => {
      visible.value = true;
      await init();
    };

    const onConfirm = async () => {
      if (/[^\w@+-.]/g.test(form.username)) {
        ElNotification({
          type: "error",
          title: "Error",
          message: `Username should follow rules: 150 characters or fewer. Letters, digits and @/./+/-/_ only`
        });
        return;
      }
      if (form.password !== confirmPassword.value) {
        ElNotification({
          type: "error",
          title: "Error",
          message: `Confirm password is not the same as password.`
        });
        return;
      }
      const params: CreateMembersParams = {
        username: form.username,
        password: form.password,
        email: form.email,
        teams: form.teams
      };
      await postCreateMembers(params);
      ElNotification({
        type: "success",
        title: "Success",
        message: `Add colleague ${form.username} successful.`
      });
      emit("load");

      visible.value = false;
    };

    return {
      form,
      confirmPassword,
      teamOptions,
      visible,

      open,
      onConfirm
    };
  }
});

export default Component;
